'use client';

import { useEffect, useRef, useState } from 'react';
import CreativeEngine from '@cesdk/engine';
import { SignedIn, SignedOut, UserButton } from '@clerk/nextjs';

interface ExportResult {
  fileType: string;
  data: Blob;
}

interface EngineError {
  message: string;
  code?: string;
}

export default function CanvaEditor() {
  const containerRef = useRef<HTMLDivElement>(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    let engine: CreativeEngine | null = null;

    const initializeEditor = async () => {
      if (!containerRef.current) return;

      try {
        engine = await CreativeEngine.init({
          container: containerRef.current,
          license: process.env.NEXT_PUBLIC_CREATIVE_SDK_LICENSE || '',
          callbacks: {
            onExport: (result: ExportResult) => {
              console.log('Export result:', result);
            },
            onError: (error: EngineError) => {
              console.error('Editor error:', error);
            },
          },
        });

        // Set up initial canvas
        const scene = engine.scene.create();
        engine.scene.loadFromURL(scene, '/templates/blank.scene');
        setIsLoading(false);
      } catch (error) {
        console.error('Failed to initialize editor:', error);
        setIsLoading(false);
      }
    };

    initializeEditor();

    return () => {
      if (engine) {
        engine.dispose();
      }
    };
  }, []);

  return (
    <div className="flex flex-col min-h-screen bg-gray-100">
      <header className="bg-white shadow-md p-4 flex justify-between items-center">
        <h1 className="text-xl font-bold">Canva Editor</h1>
        <SignedIn>
          <div className="flex items-center space-x-4">
            <UserButton afterSignOutUrl="/" />
          </div>
        </SignedIn>
        <SignedOut>
          <p className="text-gray-500">You are signed out. Please sign in to continue.</p>
        </SignedOut>
      </header>

      <main className="flex-1 p-4">
        {isLoading ? (
          <div className="flex items-center justify-center h-full">
            <div className="animate-spin rounded-full h-32 w-32 border-b-2 border-gray-900"></div>
          </div>
        ) : (
          <div ref={containerRef} className="w-full h-[calc(100vh-8rem)] rounded-lg shadow-lg" />
        )}
      </main>
    </div>
  );
} 